'use client';
import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Plus, Upload } from 'lucide-react';
import { useMockDatabase } from '@/lib/data';
import EventCalendar from '@/components/admin/EventCalendar';
import CreateEventModal from '@/components/admin/CreateEventModal';
import EventDetailsModal from '@/components/admin/EventDetailsModal';
import { Header } from '@/components/layout/Header';
import type { Event } from '@/types';
import ImportIcsModal from '@/components/admin/ImportIcsModal';
import { parseIcsContent } from '@/ai/flows/parse-ics-flow';
import Link from 'next/link';
import { Users } from 'lucide-react';

export default function AdminPage() {
  const db = useMockDatabase();
  const [isCreateModalOpen, setCreateModalOpen] = useState(false);
  const [isImportModalOpen, setImportModalOpen] = useState(false);
  const [selectedEvent, setSelectedEvent] = useState<Event | null>(null);

  const handleEventSelect = (event: Event) => {
    const fullEvent = db.events.find(e => e.id === event.id);
    setSelectedEvent(fullEvent || null);
  };
  
  const handleModalClose = () => {
    setSelectedEvent(null)
  }

  const handleCreateModalClose = () => {
    setCreateModalOpen(false)
  }

  const handleImportModalClose = () => {
    setImportModalOpen(false);
  }

  return (
    <div className="flex min-h-screen w-full flex-col bg-muted/40">
      <Header />
      <main className="flex flex-1 flex-col gap-4 p-4 md:gap-8 md:p-8">
        <div className="flex items-center justify-between">
          <h1 className="text-3xl font-bold font-headline tracking-tight">
            Admin Console
          </h1>
          <div className="flex gap-2">
            <Button variant="outline" onClick={() => setImportModalOpen(true)}>
              <Upload className="mr-2 h-4 w-4" /> Import from ICS
            </Button>
            <Button onClick={() => setCreateModalOpen(true)}>
              <Plus className="mr-2 h-4 w-4" /> Create New Service
            </Button>
            <Button asChild variant="outline">
              <Link href="/admin/volunteers">
                <Users className="mr-2 h-4 w-4" /> View Volunteers
              </Link>
            </Button>
          </div>
        </div>
        <EventCalendar events={db.events.filter(e => e.churchId === db.MOCK_CHURCH_ID)} onEventClick={handleEventSelect} />
      </main>
      <CreateEventModal
        isOpen={isCreateModalOpen}
        onClose={handleCreateModalClose}
        createEvent={db.createEvent}
      />
      <ImportIcsModal
        isOpen={isImportModalOpen}
        onClose={handleImportModalClose}
        importAction={parseIcsContent}
        createEvent={db.createEvent}
      />
      {selectedEvent && (
        <EventDetailsModal
          key={selectedEvent.id}
          event={selectedEvent}
          isOpen={!!selectedEvent}
          onClose={handleModalClose}
          db={db}
        />
      )}
    </div>
  );
}
