'use client';

import { useState } from 'react';
import { format, isSameDay, isSameMonth } from 'date-fns';
import { Calendar } from '@/components/ui/calendar';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import type { Event } from '@/types';

interface EventCalendarProps {
  events: Event[];
  onEventClick: (event: Event) => void;
}

export default function EventCalendar({ events, onEventClick }: EventCalendarProps) {
  const [currentMonth, setCurrentMonth] = useState(new Date());
  
  const eventsByDay = events.reduce((acc, event) => {
    const day = format(event.eventDate, 'yyyy-MM-dd');
    if (!acc[day]) {
      acc[day] = [];
    }
    acc[day].push(event);
    return acc;
  }, {} as Record<string, Event[]>);

  const DayWithEvents = ({ date, displayMonth }: { date: Date, displayMonth: Date }) => {
    const dayEvents = eventsByDay[format(date, 'yyyy-MM-dd')];
    const isCurrentMonth = isSameMonth(date, displayMonth);

    return (
      <div className="relative h-full w-full flex flex-col items-start justify-start p-1">
        <span className={!isCurrentMonth ? "text-muted-foreground" : ""}>
          {format(date, 'd')}
        </span>
        {dayEvents && isCurrentMonth && (
          <div className="flex flex-wrap gap-1 mt-1">
            {dayEvents.slice(0, 2).map((event) => (
              <div key={event.id} className="h-1.5 w-1.5 rounded-full bg-primary" />
            ))}
             {dayEvents.length > 2 && <div className="h-1.5 w-1.5 rounded-full bg-muted-foreground" />}
          </div>
        )}
      </div>
    );
  };
  
  const [selectedDate, setSelectedDate] = useState<Date | undefined>(new Date());
  const selectedDayEvents = selectedDate ? events.filter(e => isSameDay(e.eventDate, selectedDate)).sort((a,b) => a.eventDate.getTime() - b.eventDate.getTime()) : [];

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 items-start">
      <Card className="lg:col-span-2 shadow-lg bg-card">
        <Calendar
          mode="single"
          selected={selectedDate}
          onSelect={setSelectedDate}
          month={currentMonth}
          onMonthChange={setCurrentMonth}
          className="p-0"
          classNames={{
            months: "flex flex-col sm:flex-row",
            month: "space-y-4 p-3",
            caption_label: "text-lg font-bold font-headline",
            head_cell: "w-full font-semibold",
            table: "w-full border-collapse space-y-1",
            head_row: "flex",
            row: "flex w-full mt-2",
            cell: "h-24 text-center text-sm p-0 relative [&:has([aria-selected])]:bg-accent/20 first:[&:has([aria-selected])]:rounded-l-md last:[&:has([aria-selected])]:rounded-r-md focus-within:relative focus-within:z-20",
            day: "h-full w-full p-0 justify-start items-start font-normal aria-selected:opacity-100 rounded-md hover:bg-accent/10",
          }}
          components={{
            Day: ({ date, displayMonth }) => <DayWithEvents date={date} displayMonth={displayMonth} />,
          }}
        />
      </Card>
      <Card className="shadow-lg bg-card sticky top-20">
        <CardHeader>
          <CardTitle className="font-headline">
            {selectedDate ? format(selectedDate, 'EEEE, MMMM do') : 'Select a date'}
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-3 max-h-[60vh] overflow-y-auto">
          {selectedDayEvents.length > 0 ? (
            selectedDayEvents.map(event => (
              <div key={event.id} className="p-3 rounded-lg border bg-card hover:bg-muted/50 cursor-pointer transition-colors" onClick={() => onEventClick(event)}>
                <h4 className="font-semibold">{event.eventName}</h4>
                <p className="text-sm text-muted-foreground">{format(event.eventDate, 'p')}</p>
                <div className="flex flex-wrap gap-1 mt-2">
                  {event.roles.slice(0,3).map(role => <Badge key={role.id} variant="secondary">{role.roleName}</Badge>)}
                  {event.roles.length > 3 && <Badge variant="secondary">+{event.roles.length-3}</Badge>}
                </div>
              </div>
            ))
          ) : (
            <p className="text-muted-foreground py-8 text-center">No events scheduled for this day.</p>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
