'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Church, Users, Calendar } from 'lucide-react';
import { cn } from '@/lib/utils';

export function Header() {
  const pathname = usePathname();

  const navLinks = [
    { href: '/admin', label: 'Admin Calendar', icon: Calendar },
    { href: '/my-schedule', label: 'My Schedule', icon: Users },
  ];

  return (
    <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container flex h-14 items-center">
        <Link href="/" className="mr-6 flex items-center space-x-2">
          <Church className="h-6 w-6 text-primary" />
          <span className="font-bold font-headline sm:inline-block">Weaver's Loom</span>
        </Link>
        <nav className="flex items-center space-x-2">
          {navLinks.map(({ href, label, icon: Icon }) => (
            <Button
              key={href}
              variant={pathname === href ? 'secondary' : 'ghost'}
              className={cn(
                'justify-start',
                pathname === href && 'font-bold'
              )}
              asChild
            >
              <Link href={href}>
                <Icon className="mr-2 h-4 w-4" />
                <span className="hidden sm:inline">{label}</span>
              </Link>
            </Button>
          ))}
        </nav>
      </div>
    </header>
  );
}
