'use client';
import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogDescription } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Loader2, Upload } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import type { ParsedEvent, Event } from '@/types';

interface ImportIcsModalProps {
  isOpen: boolean;
  onClose: () => void;
  importAction: (icsContent: string) => Promise<ParsedEvent[]>;
  createEvent: (newEventData: Omit<Event, 'id' | 'roles' | 'churchId'>) => Promise<Event>;
}

export default function ImportIcsModal({ isOpen, onClose, importAction, createEvent }: ImportIcsModalProps) {
  const { toast } = useToast();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [selectedFile, setSelectedFile] = useState<File | null>(null);

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files) {
      setSelectedFile(event.target.files[0]);
    }
  };

  const handleSubmit = async () => {
    if (!selectedFile) {
      toast({
        variant: 'destructive',
        title: 'No file selected',
        description: 'Please select an .ics file to import.',
      });
      return;
    }

    setIsSubmitting(true);
    const reader = new FileReader();

    reader.onload = async (e) => {
      const content = e.target?.result as string;
      if (!content) {
        toast({ variant: 'destructive', title: 'Error', description: 'Could not read file content.' });
        setIsSubmitting(false);
        return;
      }
      try {
        const parsedEvents = await importAction(content);
        
        for (const pEvent of parsedEvents) {
          await createEvent({
            eventName: pEvent.eventName,
            eventDate: new Date(pEvent.eventDate),
            notes: pEvent.notes
          });
        }

        toast({
          title: 'Import Successful',
          description: `Successfully imported ${parsedEvents.length} events.`,
        });
        onClose();
        setSelectedFile(null);
      } catch (error) {
        toast({
          variant: 'destructive',
          title: 'Import Failed',
          description: 'Could not parse the ICS file. Please check the file format and try again.',
        });
      } finally {
        setIsSubmitting(false);
      }
    };

    reader.onerror = () => {
      toast({ variant: 'destructive', title: 'Error', description: 'Failed to read the selected file.' });
      setIsSubmitting(false);
    };
    
    reader.readAsText(selectedFile);
  };

  return (
    <Dialog open={isOpen} onOpenChange={(open) => {
        if (!open) {
            setSelectedFile(null);
        }
        onClose();
    }}>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle className="font-headline">Import from ICS file</DialogTitle>
          <DialogDescription>Upload a .ics calendar file to import events.</DialogDescription>
        </DialogHeader>
        <div className="grid gap-4 py-4">
          <div className="grid w-full max-w-sm items-center gap-1.5">
            <Label htmlFor="ics-file">ICS File</Label>
            <Input id="ics-file" type="file" accept=".ics" onChange={handleFileChange} />
          </div>
          {selectedFile && <p className="text-sm text-muted-foreground">Selected: {selectedFile.name}</p>}
        </div>
        <DialogFooter>
          <Button type="button" variant="ghost" onClick={onClose} disabled={isSubmitting}>Cancel</Button>
          <Button onClick={handleSubmit} disabled={isSubmitting || !selectedFile}>
            {isSubmitting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Upload className="mr-2 h-4 w-4" />}
            Import Events
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
