'use server';
/**
 * @fileOverview Parses ICS calendar content into a structured event format.
 * 
 * - parseIcsContent - A function that handles parsing of ICS content.
 * - ParsedEvent - The return type for each parsed event.
 */

import { ai } from '@/ai/genkit';
import { z } from 'zod';
import type { ParsedEvent } from '@/types';

export { ParsedEvent };

const IcsParseOutputSchema = z.array(
    z.object({
        eventName: z.string().describe('The summary or title of the event.'),
        eventDate: z.string().datetime().describe('The start date and time of the event in ISO 8601 format.'),
        notes: z.string().optional().describe('The description or notes for the event.'),
    })
);

export async function parseIcsContent(icsContent: string): Promise<ParsedEvent[]> {
    const prompt = ai.definePrompt({
        name: 'icsParserPrompt',
        input: { schema: z.string() },
        output: { schema: IcsParseOutputSchema },
        prompt: `You are an expert at parsing iCalendar (.ics) data. Parse the following ICS data and extract the events. For each event, provide the event summary (VEVENT:SUMMARY), start date (VEVENT:DTSTART), and description (VEVENT:DESCRIPTION). Convert DTSTART to a full ISO 8601 UTC timestamp.

ICS Data:
{{{input}}}`,
    });
    
    const { output } = await prompt(icsContent);
    return output || [];
}
